@import url('https://fonts.googleapis.com/css?family=Roboto:300,400');

@font-face {
  font-family: 'vanadine';
  src: url('./static/fonts/vanadine_bold-webfont.eot');
  src: url('/static/fonts/vanadine_bold-webfont.eot?#iefix') format('embedded-opentype'),
  url('/static/fonts/vanadine_bold-webfont.woff2') format('woff2'),
  url('/static/fonts/vanadine_bold-webfont.woff') format('woff'),
  url('/static/fonts/vanadine_bold-webfont.ttf') format('truetype'),
  url('/static/fonts/vanadine_bold-webfont.svg#vanadinebold') format('svg');
  font-weight: normal;
  font-style: normal;
}




body{
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 300;
}


p{

  font-weight: 300;
  font-size: 20px;
  margin: 0 0 15px 0;
  line-height: 30px;

}

a{

  text-decoration: none;
  transition: all .3s ease-in;


  a{

    &:hover{

      color: $pink;

      svg{
        fill: $pink;
      }
    }

  }

}